.modal {
  display: none;
  &__body {
    max-width: 590px;
    height: 574px;
    //position: fixed
    //left: 50%
    //transform: translateX(-50%)
    top: 40px;
    //z-index: 10000
    box-sizing: border-box;
    background: url(../img/modal_bg.jpg) no-repeat;
    background-size: cover;
    padding: 30px 120px;
    @include respond-to(566) {
      padding: 30px 85px; }
    @include respond-to(480) {
      padding: 30px 20px; }
    @include respond-to(360) {
      padding: 30px 10px; } }
  &__close {
    width: 13px;
    height: 13px;
    background: url(../img/modal_close.png) no-repeat center center;
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer; }
  &__header {
    font-family: $copiright;
    font-size: 48px;
    font-weight: 400;
    color: $white;
    @include respond-to(480) {
      font-size: 43px; } }
  &__form {
    width: 90%;
    box-sizing: border-box;
    display: block;
    margin: 0 auto;
    padding-top: 50px; }
  &__input {
    width: 100%;
    height: 45px;
    background: $lightBlack2;
    border: 2px solid $lightBlack1;
    margin-bottom: 18px;
    font-family: $coco;
    font-size: 19px;
    color: $white;
    box-sizing: border-box;
    padding-left: 15px;
    &::placeholder {
      color: $white;
      text-align: center; } }
  &__btn {
    width: 289px;
    height: 95px;
    background: url(../img/modal_btn.png) no-repeat center center;
    border: none;
    font-family: $coco;
    font-size: 22px;
    font-weight: 400;
    color: $white;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.7);
    text-transform: uppercase;
    cursor: pointer;
    position: relative;
    margin-top: 40px;
    &:before {
      content: "";
      width: 368px;
      height: 118px;
      background: url(../img/modal_btn_bd.png) no-repeat;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1; }
    &:hover {
      transition: .6s ease-in-out;
      &:before {
        display: none;
        transition: .6s ease-in-out; } } } }

.fancybox-slide--html .fancybox-close-small {
  color: $white !important; }

