.rules {
  background: url(../img/bg4.png) no-repeat center center;
  background-size: cover;
  z-index: 3;
  margin-top: -227px;
  padding-top: 227px;
  box-sizing: border-box;
  &:after {
    content: "";
    width: 169px;
    height: 111px;
    background: url(../img/plane.png) no-repeat;
    position: absolute;
    bottom: -85px;
    right: 28%; }
  &__header {
    font-family: $copiright;
    font-size: 48px;
    font-weight: 400;
    color: $white;
    text-align: center;
    @include respond-to(992) {
      display: none; }
    @include respond-to(480) {
      font-size: 31px; }
    &--mob {
      display: none;
      margin-top: 50px;
      @include respond-to(992) {
        display: block; } } }
  &__wrapper {
    position: relative;
    width: 100%;
    margin-top: 60px;
    padding-bottom: 115px; }
  &__img-block {
    display: inline-block;
    position: relative;
    left: 50%;
    transform: translateX(-50%); }
  &__items {
    position: absolute;
    width: 100%;
    left: 0;
    right: 0;
    top: 117px;
    @include respond-to(992) {
      display: none; }
    &--mob {
      position: relative;
      display: none;
      margin-top: 50px;
      @include respond-to(992) {
        display: block; } } }
  &__item {
    position: absolute;
    display: inline-block;
    font-family: $coco;
    font-size: 19px;
    font-weight: 400;
    color: $white;
    &--mob {
      position: relative;
      display: block;
      //width: 317px
      font-family: $coco;
      font-size: 19px;
      font-weight: 400;
      color: $white;
      margin-bottom: 50px;
      @include respond-to(480) {
        font-size: 16px;
        margin-bottom: 80px; } } }
  &__item1 {
    top: 0;
    left: 0;
    padding-left: 80px;
    &:before {
      content: "";
      width: 110px;
      height: 122px;
      background: url(../img/icon_rules1.png);
      position: absolute;
      top: -38px;
      left: 0; }
    &:after {
      content: "";
      width: 315px;
      height: 14px;
      background: url(../img/icon_rules1_stroke.png) no-repeat;
      position: absolute;
      bottom: -33px;
      left: 30px; }
    &--mob {
      padding-left: 80px;
      &:before {
        content: "";
        width: 110px;
        height: 122px;
        background: url(../img/icon_rules1.png);
        position: absolute;
        top: -38px;
        left: 0; }
      &:after {
        content: "";
        width: 315px;
        height: 14px;
        background: url(../img/icon_rules1_stroke.png) no-repeat;
        position: absolute;
        bottom: -33px;
        left: 30px; } } }
  &__item2 {
    top: 0;
    right: 0;
    padding-right: 74px;
    &:before {
      content: "";
      width: 110px;
      height: 122px;
      background: url(../img/icon_rules3.png);
      position: absolute;
      top: -38px;
      right: 0; }
    &:after {
      content: "";
      width: 315px;
      height: 14px;
      background: url(../img/icon_rules3_stroke.png) no-repeat;
      position: absolute;
      bottom: -33px;
      right: 30px; }
    &--mob {
      padding-right: 74px;
      float: right;
      &:before {
        content: "";
        width: 110px;
        height: 122px;
        background: url(../img/icon_rules3.png);
        position: absolute;
        top: -38px;
        right: 0; }
      &:after {
        content: "";
        width: 315px;
        height: 14px;
        background: url(../img/icon_rules3_stroke.png) no-repeat;
        position: absolute;
        bottom: -33px;
        right: 30px; } } }
  &__item3 {
    top: 135px;
    left: 0;
    padding-left: 80px;
    &:before {
      content: "";
      width: 110px;
      height: 122px;
      background: url(../img/icon_rules2.png);
      position: absolute;
      top: -38px;
      left: 0; }
    &:after {
      content: "";
      width: 315px;
      height: 14px;
      background: url(../img/icon_rules2_stroke.png) no-repeat;
      position: absolute;
      bottom: -33px;
      left: 30px; }
    &--mob {
      padding-left: 80px;
      &:before {
        content: "";
        width: 110px;
        height: 122px;
        background: url(../img/icon_rules2.png);
        position: absolute;
        top: -38px;
        left: 0; }
      &:after {
        content: "";
        width: 315px;
        height: 14px;
        background: url(../img/icon_rules2_stroke.png) no-repeat;
        position: absolute;
        bottom: -33px;
        left: 30px; } } }
  &__item4 {
    top: 135px;
    right: 0;
    padding-right: 74px;
    &:before {
      content: "";
      width: 110px;
      height: 122px;
      background: url(../img/icon_rules4.png);
      position: absolute;
      top: -38px;
      right: 0; }
    &:after {
      content: "";
      width: 315px;
      height: 14px;
      background: url(../img/icon_rules4_stroke.png) no-repeat;
      position: absolute;
      bottom: -33px;
      right: 30px; }
    &--mob {
      padding-right: 74px;
      float: right;
      &:before {
        content: "";
        width: 110px;
        height: 122px;
        background: url(../img/icon_rules4.png);
        position: absolute;
        top: -38px;
        right: 0; }
      &:after {
        content: "";
        width: 315px;
        height: 14px;
        background: url(../img/icon_rules4_stroke.png) no-repeat;
        position: absolute;
        bottom: -33px;
        right: 30px; } } }
  &__btn-block {
    width: 370px;
    height: 120px;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 50px;
    @include respond-to(480) {
      width: 262px;
      height: 80px; } }
  &__btn {
    display: inline-block;
    width: 327px;
    height: 83px;
    background: url(../img/header_btn.png) no-repeat;
    text-decoration: none;
    font-family: $coco;
    font-size: 22px;
    font-weight: 700;
    color: $white;
    text-transform: uppercase;
    line-height: 83px;
    text-align: center;
    opacity: .999;
    position: relative;
    top: 0;
    left: 0;
    //transform: translateX(-50%)
    z-index: 1;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.7);
    @include respond-to(480) {
      background: url(../img/header_btn_mob.png) no-repeat;
      font-size: 20px;
      width: 264px;
      height: 82px; }
    &:before {
      content: "";
      width: 368px;
      height: 118px;
      background: url(../img/header_btn_bd.png) no-repeat;
      position: absolute;
      top: -16px;
      left: -21px;
      z-index: 1;
      @include respond-to(480) {
        background: url(../img/header_btn_bd_mob.png) no-repeat;
        top: 0;
        left: 0; } } } }
