.skills {
  background: url(../img/bg3.png) no-repeat center center;
  background-size: cover;
  margin-top: -50px;
  padding-top: 100px;
  padding-bottom: 150px;
  box-sizing: border-box;
  z-index: 4;
  &__wrapper {
    width: 100%;
    @include respond-to(992) {
      flex-wrap: wrap-reverse;
      justify-content: space-around; } }
  &__header {
    display: block;
    margin-top: 50px;
    font-family: $copiright;
    font-size: 48px;
    font-weight: 400;
    color: $white;
    @include respond-to(992) {
      text-align: center; }
    @include respond-to(480) {
      font-size: 30px; } }
  &__left {
    width: 57%;
    min-width: 560px;
    @include respond-to(992) {
      width: 100%;
      min-width: 290px; } }
  &__right {
    width: 35%;
    min-width: 340px;
    @include respond-to(992) {
      width: 100%;
      min-width: 290px; } }
  &__items {
    width: 100%;
    column-count: 2;
    margin-top: 85px;
    @include respond-to(728) {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around; }
    @include respond-to(720) {
      justify-content: center;
      column-count: 1; }
    @include respond-to(480) {
      display: none; }
    &--mob {
      display: none;
      width: 100%;
      column-count: 1;
      margin-top: 55px;
      flex-wrap: wrap;
      //flex-direction: column
      justify-content: space-around;
      @include respond-to(480) {
        display: flex; } } }
  &__item {
    width: 366px;
    height: 150px;
    //background-repeat: no-repeat
    display: inline-block;
    box-sizing: border-box;
    font-family: $coco;
    font-size: 19px;
    font-weight: 600;
    color: $grey;
    position: relative;
    &--mob {
      width: 262px;
      height: 109px;
      background-repeat: no-repeat;
      display: inline-block;
      box-sizing: border-box;
      font-family: $coco;
      font-size: 14px;
      font-weight: 600;
      color: $grey; } }
  &__item1 {
    //background-image: url(../img/form_photo.png)
    //background-position: -29px 0
    //padding-top: 45px
    //padding-left: 65px
    &--mob {
      background-image: url(../img/form_photo_mob.png);
      background-position: 0 0;
      padding-top: 40px;
      padding-left: 56px; } }
  &__item2 {
    //background-image: url(../img/form_phone.png)
    //background-position: -29px 0
    //padding-top: 63px
    //padding-left: 65px
    &--mob {
      background-image: url(../img/form_phone_mob.png);
      background-position: 0 0;
      padding-top: 36px;
      padding-left: 56px; } }
  &__item3 {
    //background-image: url(../img/form_gun.png)
    //background-position: 0 0
    //padding-top: 45px
    //padding-left: 95px
    &--mob {
      background-image: url(../img/form_gun_mob.png);
      background-position: 0 0;
      padding-top: 29px;
      padding-left: 54px; }
    @include respond-to(720) {
      transform: translateX(-28px); } }
  &__item4 {
    //background-image: url(../img/form_fist.png)
    //background-position: 13px 0
    //padding-top: 52px
    //padding-left: 95px
    &--mob {
      background-image: url(../img/form_fist_mob.png);
      background-position: 0 0;
      padding-top: 28px;
      padding-left: 54px; }
    @include respond-to(720) {
      transform: translateX(-28px); } }
  &__img1 {
    position: absolute;
    top: -26px;
    left: -36px;
    z-index: 0; }
  &__txt1 {
    position: absolute;
    top: 48px;
    left: 67px;
    z-index: 1;
    object-fit: cover; }
  &__img2 {
    position: absolute;
    top: -27px;
    left: -36px;
    z-index: 0; }
  &__txt2 {
    position: absolute;
    object-fit: cover;
    top: 48px;
    left: 67px;
    z-index: 1; }
  &__img3 {
    position: absolute;
    top: -26px;
    left: -5px;
    z-index: 0; }
  &__txt3 {
    position: absolute;
    object-fit: cover;
    top: 48px;
    left: 95px;
    z-index: 1; }
  &__img4 {
    position: absolute;
    top: -27px;
    left: -5px;
    z-index: 0; }
  &__txt4 {
    position: absolute;
    object-fit: cover;
    top: 48px;
    left: 95px;
    z-index: 1; }
  &__pers {
    img {
      max-width: 409px; }
    @include respond-to(992) {
      display: flex;
      justify-content: center; } } }
