.header {
  width: 100%;
  background: url(../img/bg_header.png) no-repeat center center;
  position: relative;
  background-size: cover;
  z-index: 6;
  @include respond-to(1097) {
    padding-bottom: 200px; }
  &__wrapper {
    width: 100%;
    @include respond-to(1097) {
      justify-content: center;
      flex-wrap: wrap-reverse; } }
  &__content {
    width: 100%;
    margin-top: 80px;
    @include respond-to(1097) {
      margin-top: 50px; } }
  &__left {
    width: 42%;
    @include respond-to(1122) {
      min-width: 456px; }
    @include respond-to(480) {
      min-width: 290px; }
    @include respond-to(1097) {
      width: 100%; } }
  &__right {
    width: 56%;
    @include respond-to(1122) {
      min-width: 611px; }
    @include respond-to(604) {
      width: 100%;
      min-width: 290px; } }
  &__img {
    transform: translateY(-125px);
    @include respond-to(1097) {
      transform: none; } }
  &__text {
    position: relative;
    @include respond-to(1097) {
      text-align: center;
      z-index: 1;
      opacity: .999; }
    h1 {
      display: inline-block;
      font-family: $cocoExtra;
      font-size: 50px;
      font-weight: 400;
      @include respond-to(1097) {
        display: none; }
      @include respond-to(633) {
        font-size: 40px; }
      @include respond-to(513) {
        font-size: 35px; }
      @include respond-to(480) {
        font-size: 23px; } }
    span {
      display: inline-block;
      font-family: $coco;
      font-size: 25px;
      font-weight: 600;
      padding-top: 21px;
      @include respond-to(1097) {
        font-size: 30px; }
      @include respond-to(513) {
        font-size: 24px; }
      @include respond-to(480) {
        font-size: 18px; } }
    &:after {
      content: "";
      width: 326px;
      height: 272px;
      background: url(../img/header_arrow.png) no-repeat;
      position: absolute;
      right: -7px;
      top: 75px;
      z-index: 1;
      @include respond-to(1137) {
        right: -19px; }
      @include respond-to(1097) {
        display: none; } } }
  &__btn-block {
    width: 370px;
    height: 120px;
    margin-top: 137px;
    margin-left: -10px;
    position: relative;
    @include respond-to(1097) {
      margin-top: 70px;
      left: 50%;
      transform: translateX(-50%); }
    @include respond-to(480) {
      width: 264px;
      height: 82px; }
    &:after {
      content: "";
      width: 622px;
      height: 355px;
      position: absolute;
      top: -104px;
      left: -96px;
      background: url(../img/header_points.png) no-repeat;
      z-index: 0;
      @include respond-to(1097) {
        top: -154px; } } }
  &__btn {
    display: inline-block;
    width: 327px;
    height: 83px;
    background: url(../img/header_btn.png) no-repeat;
    text-decoration: none;
    font-family: $coco;
    font-size: 22px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 83px;
    text-align: center;
    opacity: .999;
    position: absolute;
    top: 17px;
    left: 21px;
    z-index: 1;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.7);
    @include respond-to(480) {
      background: url(../img/header_btn_mob.png) no-repeat;
      font-size: 20px;
      width: 264px;
      height: 82px; }
    &:before {
      content: "";
      width: 368px;
      height: 118px;
      background: url(../img/header_btn_bd.png) no-repeat;
      position: absolute;
      top: -16px;
      left: -21px;
      z-index: 1;
      @include respond-to(480) {
        background: url(../img/header_btn_bd_mob.png) no-repeat;
        top: 0;
        left: 0; } }
    &:hover {
      &:after {
        box-shadow: 0 0 50px rgba(40,87,38,0.7);
        transition: .5s; } } }

  .progress-container {
    width: 100%;
    height: 3px;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 9999;
    @include respond-to(680) {
      display: none; }
    .progress-none {
      width: 100%;
      background: $white;
      opacity: .4;
      height: inherit;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      z-index: 9000; }
    .progress-line {
      width: 0%;
      background: $blue3;
      height: inherit;
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      z-index: 9555; } }
  .top-line {
    padding-top: 40px;
    width: 100%;
    .logo {
      display: block;
      @include respond-to(650) {
        width: 200px; } } }

  .social-block {
    width: 155px;
    .soc-icon {
      width: 41px;
      height: 41px;
      border-radius: 50%;
      transition: .5s;
      &:hover {
        box-shadow: 0 0 10px rgba(255, 255, 255, .5);
        transition: .5s; } } }

  .title-mob {
    display: none;
    @include respond-to(1097) {
      display: block; } } }


