@import "./libs/jquery.fancybox";

.parallax-viewport {
  position: relative     /* relative, absolute, fixed */;
  overflow: hidden; }

.parallax-layer {
  position: absolute;
  left: 0;
  top: 52px;
  @include respond-to(992) {
    display: none; } }

.parallax-layer2 {
  position: absolute;
  left: 0;
  bottom: 10px;
  z-index: 10;
  @include respond-to(992) {
    display: none; } }

